@import url(https://fonts.googleapis.com/css2?family=Exo:wght@200&family=Jost:wght@300&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



:root{
  --electric-green: #00FF00ff;
  --blue-munsell: #008FA2ff;
  --dark-green: #008000ff;
  --background: #212f45;
  
}

body{
  font-family: 'Jost', sans-serif;
  background-color: #212f45;
  background-color: var(--background);
  color: #2ec4b6;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.App {
  text-align: center;
}
.Header{
    font-size: 10px;
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
}
.Header-img{
    width: 300px;
}
button{
    background-color: transparent;
    color: var(--electric-green);
    opacity: 0.9;
    font-weight: 1000;
    font-size: 18px;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    border: none;
    position: absolute;
    z-index: 2;
    right: -1em;
    top: 25%;
    padding: 2px;
}
.button-img{
    width: 1.5em;
    border-radius: 50%;
    justify-self: center;
}
.prev{
    left: 1em;
}
.ChScroll-Container{
    background-color: var(--background);
    width: 350px;
    height: 65px;
    border-radius: 30px;
    position: relative;
    margin-top: 15px;
    padding-top: 5px;
    
}
.ChScroll-Container::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    -webkit-filter: blur(3px);
            filter: blur(3px);
    background-color: var(--blue-munsell);
    width: 100%;
    height: 70px;
    border-radius: 30px;
}
.ChScroll-ul{
    -webkit-margin-start: 0;
            margin-inline-start: 0;
    -webkit-margin-after: 0;
            margin-block-end: 0;
    -webkit-margin-before: 0;
            margin-block-start: 0;
    -webkit-margin-end: 0;
            margin-inline-end: 0;
    list-style: none;
    display: flex;
    flex-direction: row;
    align-content: center;
    padding: 0;
    overflow-y: hidden; 
    overflow-x: auto;
    border-radius: 30px;
}
.ChScroll-li{
    cursor: pointer;
    margin-left: 8px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
}
.ChScroll-ChImg-Alive{
    background-color: white;
    width: 55px;
    height: 55px;
    border-radius: 50%;
    z-index: 2;
    position: relative;
}
.ChScroll-ChImg-Alive::before{
    content: "";
    background-color: var(--electric-green);
    width: 55px;
    height: 55px;
    border-radius: 50%;
    z-index: -1;
    position: absolute;
    -webkit-filter: blur(3px);
            filter: blur(3px);
    top: 0;
    left: 0;
}
.ChScroll-ChImg-Dead{
    background-color: white;
    width: 55px;
    height: 55px;
    border-radius: 50%;
    z-index: 2;
    position: relative;
}
.ChScroll-ChImg-Dead::before{
    content: "";
    background-color: red;
    width: 55px;
    height: 55px;
    border-radius: 50%;
    z-index: -1;
    position: absolute;
    -webkit-filter: blur(4px);
            filter: blur(4px);
    top: 0;
    left: 0;
}
.ChScroll-ChImg-Unknow{
    background-color: white;
    width: 55px;
    height: 55px;
    border-radius: 50%;
    z-index: 2;
    position: relative;
}
.ChScroll-ChImg-Unknow::before{
    content: "";
    background-color: var(--blue-munsell);
    width: 55px;
    height: 55px;
    border-radius: 50%;
    z-index: -1;
    position: absolute;
    -webkit-filter: blur(4px);
            filter: blur(4px);
    top: 0;
    left: 0;
}
.Profile-Picture{
    height: 55px;
    border-radius: 50%;
    z-index: 3;
}
.Searher{
    width: 270px;
    border-radius: 10px;
    margin-top: 15px;
    padding: 10px;
    margin-bottom: 5px;
    text-align: center;
    background-color: transparent;
    font-weight: bold;
    border-style: none;
    border-bottom: 1px solid var(--blue-munsell);
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
    transition: border-color 0.3s ease;
}
.Searher:focus{
    color: white;
    outline: black;
    border-color: var(--electric-green);
}
.CardInfo-Container{
    margin-top: 20px;
    width: 330px;
    height: 355px;
    padding: 10px;
    border-radius: 30px;
    position: relative;
    background-color: var(--background);
    display: grid;
    grid-template-areas: 
    "        .        .       "
    "       pic       pic     "
    "       pic       pic     "
    "      info1      info1   "
    "      info2      info3   "
    "      info2      info3  ";
}
.CardInfo-Container::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 350px;
    height: 375px;
    border-radius: 30px;
    background-color: var(--blue-munsell);
    -webkit-filter: blur(2px);
            filter: blur(2px);
    z-index: -1;
}
.CardInfo-ProfilePicture{
    border-radius: 50%;
    width: 140px;
    grid-area: pic;
    justify-self: center;
}
.CardInfo-Info1{
    justify-self: center;
    text-align: center;
    grid-area: info1;
}
.CardInfo-Info2{
    justify-self: center;
    text-align: center;
    grid-area: info2;
}
.CardInfo-Info3{
    justify-self: center;
    text-align: center;
    grid-area: info3;
}
